<template>
  <div>
    <el-table :data="items">
      <el-table-column label="名称" prop="name" sortable></el-table-column>
      <el-table-column label="路径" prop="path" sortable></el-table-column>
      <el-table-column label="状态" prop="state" sortable></el-table-column>

      <el-table-column label="操作">
        <template #default="scope">
          <el-button
            type="primary"
            link
            @click="handleClickImportItem(scope.row)"
          >
            导入
          </el-button>
        </template>
      </el-table-column>
    </el-table>
  </div>
</template>

<script>
export default {
  name: "import-repo-state-table",

  computed: {
    items() {
      return this.$store.getters["import_repository/list"];
    },
  },

  methods: {
    handleClickImportItem(item) {
      let items = [item];
      this.$store.dispatch("import_repository/save", { items });
    },
  },
};
</script>
