<template>
  <div>
    <h1>导入单个本地仓库</h1>

    <el-input v-model="path" placeholder="输入仓库的本地路径">
      <template #prepend> 仓库路径 </template>

      <template #append>
        <el-button @click="handleClickFind">查找</el-button>
      </template>
    </el-input>

    <MyStateTable />
  </div>
</template>

<script>
import MyStateTable from "./../import-local-repo/import-repo-state-table.vue";

export default {
  name: "import-local-repository",

  components: { MyStateTable },

  data() {
    return {
      path: "",
    };
  },

  methods: {
    handleClickFind() {
      let path = this.path;
      this.$store.dispatch("import_repository/locate", { path });
    },
  },
};
</script>
